import { SelectionModel } from "@angular/cdk/collections";
import { Component, OnInit, ViewChild } from "@angular/core";
import { MatMenuTrigger } from "@angular/material/menu";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ActivatedRoute, Router } from "@angular/router";
import { Subscription, Subject, takeUntil } from "rxjs";
import { MiddlewareService } from "src/app/erp-finance/data/services/middlware.service";
import { FinanceReportsService } from "src/app/erp-reports/data/services/finance-reports.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";

@Component({
  selector: "app-gl-account-statement",
  templateUrl: "./gl-account-statement.component.html",
  styleUrls: ["./gl-account-statement.component.scss"],
})
export class GlAccountStatementComponent implements OnInit {
  displayedColumns: string[] = [
    "TrxDate",
    "ValueDate",
    "Particulars",
    "Debit",
    "Credit",
    "Closing",
    "OperatorID",
    "SupervisorID"
  ];
  

  

  //binding the columns to a table
  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  contextMenu: MatMenuTrigger;
  contextMenuPosition = { x: "0px", y: "0px" };

  subscription!: Subscription;
  selection = new SelectionModel<any>(true, []);

  isLoading: boolean = false;

  pageFunction = "View";

  glAccount = "";

  constructor(
    private route: ActivatedRoute,
    private reportService: FinanceReportsService,
    private snackbar: SnackbarService,
    private middlewareService: MiddlewareService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.route.queryParams.subscribe((params) => {
      if (params.glAccount) {
        const action = params.action;
        this.pageFunction = params.action;
        this.glAccount = params.glAccount;

        this.fromDate = params.fromDate;
        this.toDate = params.toDate;
      }
    });

    this.getData();
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  destroy$: Subject<boolean> = new Subject<boolean>();
  data: any;

  fromDate = "";
  toDate = "";
  getData() {
    this.selection = new SelectionModel<any>(true, []);
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.isLoading = true;

    let body = {
      ourBranchID: "",
      accountID: this.glAccount,
      fromDate: this.fromDate,
      toDate: this.toDate,
      chargeID: "",
      topCountTrx: ""
    };

    this.middlewareService
      .getGlAccountStatements(body)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.data :", res);
          if (res.length > 0) {
            this.data = res;
            this.isLoading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;

            console.log("this.data :", this.data);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {},
      }),
      Subscription;
  }

  refresh() {
    this.getData();
  }

  downloadGLStatementReport() {}

  posting: boolean = false;

  home() { this.router.navigate(["/erp-dashboard/main"]); }
  reportDashboard() {
    this.router.navigate(["/erp-reports/dashboard"]);
  }


  cancel() {
    this.router.navigate(["/erp-reports/dashboard"]);
  }
}
