import { DatePipe } from "@angular/common";
import { HttpParams } from "@angular/common/http";
import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
  MatDialogConfig,
} from "@angular/material/dialog";
import { Subject, Subscription, takeUntil } from "rxjs";
import { GlsLookupComponent } from "src/app/erp-finance/data/lookups/gls-lookup/gls-lookup.component";
import { FilesService } from "src/app/shared/services/files.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import { PurchaseRequisitionLookUpComponent } from "src/app/erp-finance/data/lookups/purchase-requisition-look-up/purchase-requisition-look-up.component";
import { PurchaseOrderAcknowledgementComponent } from "src/app/erp-procurement/modules/requisition/pages/purchase-order-acknowledgement/purchase-order-acknowledgement.component";
import { SuppliersLookupComponent } from "src/app/erp-procurement/modules/parameters/pages/lookups/suppliers-lookup/suppliers-lookup.component";
import { ProcurementReportsService } from "src/app/erp-reports/data/services/reports-procurement.service";

@Component({
  selector: "app-generate-procurement-report",
  templateUrl: "./generate-procurement-report.component.html",
  styleUrls: ["./generate-procurement-report.component.sass"],
})
export class GenerateProcurementReportComponent implements OnInit {
  mngForm: FormGroup;
  hide3 = true;
  agree3 = false;

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  pageFunction: string = "Add";

  hideSubmit: boolean = false;

  posting: boolean = false;

  types = [{ name: "PDF" }];

  // , { name: "EXCEL" }

  poStatuses = [{ name: "PARTIAL" }, { name: "COMPLETE" }];
  invoiceStatuses = [
    { name: "APPROVED" },
    { name: "PENDING" },
    { name: "OVERDUE" },
  ];
  transactionStatuses = [{ name: "SUCCESS" }, { name: "FAILED" }];
  constructor(
    public dialogRef: MatDialogRef<PurchaseOrderAcknowledgementComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private dialog: MatDialog,
    private filesService: FilesService,
    private datepipe: DatePipe,
    private reportService: ProcurementReportsService
  ) {}

  ngOnInit(): void {
    this.mngForm = this.createReportForm();
  }

  createReportForm(): FormGroup {
    return this.fb.group({
      type: ["PDF", Validators.required],
      fromDate: [""],
      toDate: [""],
      poStatus: ["PARTIAL"],
      prCode: [""],
      invoiceStatus: ["APPROVED"],
      supplierCode: [""],
      transactionStatus: ["SUCCESS"],
    });
  }

  ngAfterViewInit() {}

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  hideTranStatus: boolean = false;
  selectInvoiceStatus(status: string) {
    if (status === "OVERDUE") {
      this.hideTranStatus = true;
    } else {
      this.hideTranStatus = false;
    }

    console.log("this.hideTranStatus:: ", this.hideTranStatus);
  }

  //******************************************************************************************************************************************************** */
  // Look ups
  selectedItem: any[] = [];
  glsLookUp() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "Single GL Account",
      selected: this.selectedItem,
    };
    const dialogRef = this.dialog.open(GlsLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((result) => {
      console.log("result:::", result);
      result.data.forEach((element) => {
        this.mngForm.patchValue({
          accountNo: element.accountID,
        });
      });
    });
  }

  purchaseRequisitionLookUp() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "single purchaseorder",
      selected: this.selectedItem,
    };
    const dialogRef = this.dialog.open(
      PurchaseRequisitionLookUpComponent,
      dialogConfig
    );
    dialogRef.afterClosed().subscribe((res) => {
      res.data.forEach((element) => {
        this.mngForm.patchValue({
          prCode: element.purchaseRequisitionCode,
        });
      });
    });
  }
  supplierIsSelected = false;
  selectedSuppliers: any[] = [];
  suppliersLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: "single supplier",
      selected: this.selectedSuppliers,
      category: "Open",
    };

    const dialogRef = this.dialog.open(SuppliersLookupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data);

        this.supplierIsSelected = true;

        this.mngForm.patchValue({
          supplierCode: result.data[0].supplierCode,
        });
      }
    });
  }
  // **********************************************************************************************************************************************************

  response: any;
  submit() {
    this.posting = true;

    const type = this.mngForm.value.type;

    let params = new HttpParams();
    let reportName = "";

    const fromDate = this.datepipe.transform(
      this.mngForm.value.fromDate,
      "YYYY-MM-dd"
    );
    const toDate = this.datepipe.transform(
      this.mngForm.value.toDate,
      "YYYY-MM-dd"
    );
    switch (this.data.type) {
      case "POsByStatus":
        params = params
        .set("fromDate", fromDate)
        .set("toDate", toDate)
        .set("status", this.mngForm.value.poStatus);
        reportName = "PO Status";
        if (type === "PDF") {
          this.downloadPOStatusReport(params, reportName);
        } else if (type === "EXCEL") {
        }
        break;

      case "PoDeliveryReport":
        params = params
          .set("fromDate", fromDate)
          .set("toDate", toDate)
          .set("prCode", this.mngForm.value.prCode);
        reportName = "PO Delivery";
        if (type === "PDF") {
          this.downloadPODeliveryReport(params, reportName);
        } else if (type === "EXCEL") {
        }
        break;
        case "GeneralPoDeliveryReport":
          params = params
            .set("fromDate", fromDate)
            .set("toDate", toDate)
            .set("prCode", '%');
          reportName = "General PO Delivery";
          if (type === "PDF") {
            this.downloadPODeliveryReport(params, reportName);
          } else if (type === "EXCEL") {
          }
          break;


      case "SupplierAccountStatement":
        params = params
          .set("fromDate", fromDate)
          .set("toDate", toDate)
          .set("supplierCode", this.mngForm.value.supplierCode);
        reportName = "Supplier Statement";
        if (type === "PDF") {
          this.downloadSupplierStatementReport(params, reportName);
        } else if (type === "EXCEL") {
        }
        break;
      case "GeneralSupplierStatementReport":
        params = params
          .set("supplierCode", "%")
          .set("fromDate", fromDate)
          .set("toDate", toDate);
        reportName = "General Suppliers Statement";
        if (type === "PDF") {
          this.downloadGeneralSupplierStatementReport(params, reportName);
        } else if (type === "EXCEL") {
        }
        break;
      case "InvoiceReportDetailsByStatus":
        params = params
          .set("status", this.mngForm.value.invoiceStatus)
          .set("transactionStatus", this.mngForm.value.transactionStatus)
          .set("supplierCode", this.mngForm.value.supplierCode)
          .set("fromDate", fromDate)
          .set("toDate", toDate);
        reportName = "Invoice Report";
        if (type === "PDF") {
          this.downloadInvoiceDetailsReport(params, reportName);
        } else if (type === "EXCEL") {
        }
        break;

      case "GeneralInvoiceReportDetailsByStatus":
        params = params
          .set("status", this.mngForm.value.invoiceStatus)
          .set("transactionStatus", this.mngForm.value.transactionStatus)
          .set("supplierCode", "%")
          .set("fromDate", fromDate)
          .set("toDate", toDate);
        reportName = "General Invoice Report";
        if (type === "PDF") {
          this.downloadInvoiceDetailsReport(params, reportName);
        } else if (type === "EXCEL") {
        }
        break;
      //
      default:
        break;
    }
  }

  downloadPOStatusReport(params: HttpParams, reportName: string) {
    this.reportService
      .downloadPOStatusReport(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.downloadReport(res, reportName);
        },
        error: (err) => {
          this.handleError(err);
        },
      });
  }

  downloadPODeliveryReport(params: HttpParams, reportName: string) {
    this.reportService
      .downloadPODeliveryReport(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.downloadReport(res, reportName);
        },
        error: (err) => {
          this.handleError(err);
        },
      });
  }

  downloadSupplierStatementReport(params: HttpParams, reportName: string) {
    this.reportService
      .downloadSupplierStatementReport(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.downloadReport(res, reportName);
        },
        error: (err) => {
          this.handleError(err);
        },
      });
  }

  downloadGeneralSupplierStatementReport(
    params: HttpParams,
    reportName: string
  ) {
    this.reportService
      .downloadGeneralSupplierStatementReport(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.downloadReport(res, reportName);
        },
        error: (err) => {
          this.handleError(err);
        },
      });
  }

  downloadInvoiceDetailsReport(params: HttpParams, reportName: string) {
    this.reportService
      .downloadInvoiceDetailsReport(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          this.downloadReport(res, reportName);
        },
        error: (err) => {
          this.handleError(err);
        },
      });
  }

  downloadReport(res: any, reportName: string) {
    const url = window.URL.createObjectURL(res.data);
    window.open(url);
    const a = document.createElement("a");
    document.body.appendChild(a);
    a.setAttribute("style", "display: none");
    a.setAttribute("target", "_blank");
    a.href = url;
    a.download = `${reportName}.pdf`;
    a.click();
    window.URL.revokeObjectURL(url);
    a.remove();
    this.snackbar.showNotification("snackbar-success", res.message);

    this.onNoClick();
  }

  handleError(err: any) {
    this.snackbar.showNotification("snackbar-danger", err.message);
    this.posting = false;
  }

  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }
}
