import { DatePipe } from "@angular/common";
import { HttpParams } from "@angular/common/http";
import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Router, ActivatedRoute } from "@angular/router";
import { Subject, Subscription, forkJoin, takeUntil } from "rxjs";
import { FinanceReportsService } from "src/app/erp-reports/data/services/finance-reports.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";

@Component({
  selector: "app-profit-loss",
  templateUrl: "./profit-loss.component.html",
  styleUrls: ["./profit-loss.component.scss"],
})
export class ProfitLossComponent implements OnInit {
  dummy: string;
  basicInformation: any;
  buyerDetails: any;
  goodsDetails: any[] = [];
  payWayInfo: any[] = [];
  sellerDetails: any;
  extendedDetails: any;
  taxDetails: any[] = [];
  summaryDetails: any;
  qrCode: any;

  importSellerDetails: any;

  loading = true;

  Form: FormGroup;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private reportService: FinanceReportsService,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private datePipe: DatePipe
  ) {}

  pageFunction = "View";
  ngOnInit(): void {
    this.route.queryParams.subscribe((params) => {
      if (params.action) {
        const action = params.action;
        this.pageFunction = params.action;
      }
    });

    const today = new Date();
    const toDateDefault = today.toISOString().split("T")[0];
    const fromDateDefault = new Date(
      today.getFullYear(),
      today.getMonth() - 1,
      today.getDate()
    )
      .toISOString()
      .split("T")[0];
    this.Form = this.fb.group({
      type: "PDF",
      fromDate: [fromDateDefault, [Validators.required]],
      toDate: [toDateDefault, [Validators.required]],
    });

    this.Form.valueChanges.subscribe(() => {
      this.getData();
    });

    this.getData();
  }

  destroy$: Subject<boolean> = new Subject<boolean>();
  data: any;
  isLoading = false;
  getData() {
    this.isLoading = true;
    this.data = {};

    let params = {
      fromDate: this.datePipe.transform(this.Form.value.fromDate, "yyyy-MM-dd"),
      toDate: this.datePipe.transform(this.Form.value.toDate, "yyyy-MM-dd"),
    };
    this.reportService
      .getProfitAndLossReportData(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.data :", res);
          if (res.entity) {
            this.data = res.entity;
            this.isLoading = false;

            console.log("this.data :", this.data);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {
          this.isLoading = false;
        },
      }),
      Subscription;
  }

  viewDetails(data: any) {
    let route = "/erp-reports/fs-class-data";
    this.router.navigate([route], {
      queryParams: {
        fsClass: data.financial_grouping,
        fromDate: this.datePipe.transform(
          this.Form.value.fromDate,
          "yyyy-MM-dd"
        ),
        toDate: this.datePipe.transform(this.Form.value.toDate, "yyyy-MM-dd"),
        action: "View",
      },
    });
  }

  posting: boolean = false;
  downloadDoc() {
    this.posting = true;
    let type = this.Form.value.type;
    let fromDate = this.datePipe.transform(
      this.Form.value.fromDate,
      "YYYY-MM-dd"
    );
    let toDate = this.datePipe.transform(this.Form.value.toDate, "YYYY-MM-dd");

    let params = { fromDate: fromDate, toDate: toDate };
    // const params = new HttpParams()
    //   .set("fromDate", fromDate)
    //   .set("toDate", toDate);
    console.log("params :", params);

    if (type == "PDF") {
      this.reportService
        .downloadProfitAndLossReport(params)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res :", res);
            let url = window.URL.createObjectURL(res.data);

            // if you want to open PDF in new tab
            window.open(url);

            let a = document.createElement("a");
            document.body.appendChild(a);
            a.setAttribute("style", "display: none");
            a.setAttribute("target", "blank");
            a.href = url;
            // Append row.code to the download URL
            a.download = `ProfitAndLoss.pdf`;
            a.click();
            window.URL.revokeObjectURL(url);
            a.remove();
            this.snackbar.showNotification("snackbar-success", res.message);
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
            this.posting = false;
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    } else if (type == "EXCEL") {
    }
  }

  home() {
    this.router.navigate(["/erp-dashboard/main"]);
  }
  reportDashboard() {
    this.router.navigate(["/erp-reports/dashboard"]);
  }


  cancel() {
    this.router.navigate(["/erp-reports/dashboard"]);
  }
}
