import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { GenerateProcurementReportComponent } from '../dialogs/generate-procurement-report/generate-procurement-report.component';

@Component({
  selector: 'app-procurement-reports',
  templateUrl: './procurement-reports.component.html',
  styleUrls: ['./procurement-reports.component.scss']
})
export class ProcurementReportsComponent implements OnInit {

  
  constructor(private dialog: MatDialog,) { }

  ngOnInit(): void {
  }


  generateReport(type: any) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "600px";
    dialogConfig.data = {
      type: type,
    };
    const dialogRef = this.dialog.open(GenerateProcurementReportComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((result) => {
      // result.data.forEach((element) => {
      // });
    });
  }


}
