import { DatePipe } from "@angular/common";
import { HttpParams } from "@angular/common/http";
import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
  MatDialogConfig,
} from "@angular/material/dialog";
import { Subject, Subscription, takeUntil } from "rxjs";
import { GlsLookupComponent } from "src/app/erp-finance/data/lookups/gls-lookup/gls-lookup.component";
import { TransactionsService } from "src/app/erp-finance/data/services/transactions.service";
import { PurchaseOrderAcknowledgementComponent } from "src/app/erp-procurement/modules/requisition/pages/purchase-order-acknowledgement/purchase-order-acknowledgement.component";
import { FinanceReportsService } from "src/app/erp-reports/data/services/finance-reports.service";
import { FilesService } from "src/app/shared/services/files.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";

@Component({
  selector: "app-generate-finance-report",
  templateUrl: "./generate-finance-report.component.html",
  styleUrls: ["./generate-finance-report.component.sass"],
})
export class GenerateFinanceReportComponent implements OnInit {
  mngForm: FormGroup;
  hide3 = true;
  agree3 = false;

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  pageFunction: string = "Add";

  hideSubmit: boolean = false;

  posting: boolean = false;

  types = [{ name: "PDF" }];

  // , { name: "EXCEL" }

  glTypes = [{ name: "Transaction Gls" }, { name: "All Gls" }];

  imprestTypes = [{ name: "Apply" }, { name: "Accounting" }, { name: "Claim" }];

  imprestAccountings = [{ name: "Y" }, { name: "N" }];


  imprestStatuses = [
    { name: "APPROVED" },
    { name: "PENDING" },
  ];
  transactionStatuses = [{ name: "SUCCESS" }, { name: "FAILED" }];

  constructor(
    public dialogRef: MatDialogRef<PurchaseOrderAcknowledgementComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private dialog: MatDialog,
    private filesService: FilesService,
    private datepipe: DatePipe,
    private reportService: FinanceReportsService
  ) { }

  ngOnInit(): void {
    this.mngForm = this.createReportForm();
  }

  createReportForm(): FormGroup {
    return this.fb.group({
      type: ["PDF", Validators.required],
      fromDate: [""],
      toDate: [""],
      accountNo: [""],
      month: [""],
      year: [""],
      glType: [""],

      imprestType: ["Apply"],
      imprestAccounting: ["Y"],
      status: ["APPROVED"],
      transactionStatus: ["SUCCESS"],
    });
  }

  ngAfterViewInit() { }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  //******************************************************************************************************************************************************** */
  // Look ups
  selectedItem: any[] = [];
  glsLookUp() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "700px";
    dialogConfig.data = {
      action: "Single GL Account",
      selected: this.selectedItem,
    };
    const dialogRef = this.dialog.open(GlsLookupComponent, dialogConfig);
    dialogRef.afterClosed().subscribe((result) => {
      console.log("result:::", result);
      result.data.forEach((element) => {
        this.mngForm.patchValue({
          accountNo: element.accountID,
        });
      });
    });
  }

  // **********************************************************************************************************************************************************

  response: any;
  submit() {
    console.log("this.mngForm.value: ", this.mngForm.value);

    this.posting = true;

    // ProfitAndLoss
    // BalanceSheet
    let params = new HttpParams();
    const fromDate = this.datepipe.transform(
      this.mngForm.value.fromDate,
      "YYYY-MM-dd"
    );
    const toDate = this.datepipe.transform(
      this.mngForm.value.toDate,
      "YYYY-MM-dd"
    );

    if (this.data.type === "ProfitAndLoss") {
      let type = this.mngForm.value.type;
      params = params.set("fromDate", fromDate).set("toDate", toDate);
      if (type == "PDF") {
        this.reportService
          .downloadProfitAndLossReport(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res :", res);
              let url = window.URL.createObjectURL(res.data);

              // if you want to open PDF in new tab
              window.open(url);

              let a = document.createElement("a");
              document.body.appendChild(a);
              a.setAttribute("style", "display: none");
              a.setAttribute("target", "blank");
              a.href = url;
              // Append row.code to the download URL
              a.download = `ProfitAndLoss.pdf`;
              a.click();
              window.URL.revokeObjectURL(url);
              a.remove();
              this.snackbar.showNotification("snackbar-success", res.message);
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
              this.posting = false;
            },
            complete: () => {
              this.posting = false;
              this.onNoClick();
            },
          }),
          Subscription;
      } else if (type == "EXCEL") {
      }
    } else if (this.data.type === "BalanceSheet") {
      let type = this.mngForm.value.type;
      if (type == "PDF") {
        this.reportService
          .downloadBalanceSheetReport(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res :", res);
              let url = window.URL.createObjectURL(res.data);

              // if you want to open PDF in new tab
              window.open(url);

              let a = document.createElement("a");
              document.body.appendChild(a);
              a.setAttribute("style", "display: none");
              a.setAttribute("target", "blank");
              a.href = url;
              // Append row.code to the download URL
              a.download = `BalanceSheet.pdf`;
              a.click();
              window.URL.revokeObjectURL(url);
              a.remove();
              this.snackbar.showNotification("snackbar-success", res.message);
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
              this.posting = false;
            },
            complete: () => {
              this.posting = false;
              this.onNoClick();
            },
          }),
          Subscription;
      } else if (type == "EXCEL") {
      }
    } else if (this.data.type === "TrialBalance") {
      let type = this.mngForm.value.type;
      let fromDate = this.datepipe.transform(
        this.mngForm.value.fromDate,
        "YYYY-MM-dd"
      );
      let toDate = this.datepipe.transform(
        this.mngForm.value.toDate,
        "YYYY-MM-dd"
      );

      const params = new HttpParams()
        .set("fromDate", fromDate)
        .set("toDate", toDate)
        .set("reportType", this.mngForm.value.glType);

      if (type == "PDF") {
        this.reportService
          .downloadTrialBalanceReport(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res :", res);
              let url = window.URL.createObjectURL(res.data);

              // if you want to open PDF in new tab
              window.open(url);

              let a = document.createElement("a");
              document.body.appendChild(a);
              a.setAttribute("style", "display: none");
              a.setAttribute("target", "blank");
              a.href = url;
              // Append row.code to the download URL
              a.download = `TrialBalance.pdf`;
              a.click();
              window.URL.revokeObjectURL(url);
              a.remove();
              this.snackbar.showNotification("snackbar-success", res.message);
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
              this.posting = false;
            },
            complete: () => {
              this.posting = false;
              this.onNoClick();
            },
          }),
          Subscription;
      } else if (type == "EXCEL") {
      }
    } else if (this.data.type === "StatementOfAccount") {
      let type = this.mngForm.value.type;
      let fromDate = this.datepipe.transform(
        this.mngForm.value.fromDate,
        "YYYY-MM-dd"
      );
      let toDate = this.datepipe.transform(
        this.mngForm.value.toDate,
        "YYYY-MM-dd"
      );

      const params = new HttpParams()
        .set("accountNo", this.mngForm.value.accountNo)
        .set("fromDate", fromDate)
        .set("toDate", toDate);

      if (type == "PDF") {
        this.reportService
          .downloadAccountStatementReport(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res :", res);
              let url = window.URL.createObjectURL(res.data);

              // if you want to open PDF in new tab
              window.open(url);

              let a = document.createElement("a");
              document.body.appendChild(a);
              a.setAttribute("style", "display: none");
              a.setAttribute("target", "blank");
              a.href = url;
              // Append row.code to the download URL
              a.download = `StatementOfAccounts.pdf`;
              a.click();
              window.URL.revokeObjectURL(url);
              a.remove();
              this.snackbar.showNotification("snackbar-success", res.message);
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
              this.posting = false;
            },
            complete: () => {
              this.posting = false;
              this.onNoClick();
            },
          }),
          Subscription;
      } else if (type == "EXCEL") {
      }
    }

    else if (this.data.type === "ImprestAnalysis") {
      let type = this.mngForm.value.type;
      let fromDate = this.datepipe.transform(
        this.mngForm.value.fromDate,
        "YYYY-MM-dd"
      );
      let toDate = this.datepipe.transform(
        this.mngForm.value.toDate,
        "YYYY-MM-dd"
      );

      const params = new HttpParams()
        .set("accountNo", this.mngForm.value.accountNo)
        .set("fromDate", fromDate)
        .set("toDate", toDate);

      if (type == "PDF") {
        this.reportService
          .downloadAccountStatementReport(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              console.log("res :", res);
              let url = window.URL.createObjectURL(res.data);

              // if you want to open PDF in new tab
              window.open(url);

              let a = document.createElement("a");
              document.body.appendChild(a);
              a.setAttribute("style", "display: none");
              a.setAttribute("target", "blank");
              a.href = url;
              // Append row.code to the download URL
              a.download = `StatementOfAccounts.pdf`;
              a.click();
              window.URL.revokeObjectURL(url);
              a.remove();
              this.snackbar.showNotification("snackbar-success", res.message);
            },
            error: (err) => {
              this.snackbar.showNotification("snackbar-danger", err.message);
              this.posting = false;
            },
            complete: () => {
              this.posting = false;
              this.onNoClick();
            },
          }),
          Subscription;
      } else if (type == "EXCEL") {
      }
    }


  }
  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.response });
  }
}
