import { DatePipe } from "@angular/common";
import { HttpParams } from "@angular/common/http";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router, ActivatedRoute } from "@angular/router";
import { Subject, takeUntil, Subscription } from "rxjs";
import { FinanceReportsService } from "src/app/erp-reports/data/services/finance-reports.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";

@Component({
  selector: "app-trial-balance",
  templateUrl: "./trial-balance.component.html",
  styleUrls: ["./trial-balance.component.scss"],
})
export class TrialBalanceComponent implements OnInit {
  dummy: string;
  basicInformation: any;
  buyerDetails: any;
  goodsDetails: any[] = [];
  payWayInfo: any[] = [];
  sellerDetails: any;
  extendedDetails: any;
  taxDetails: any[] = [];
  summaryDetails: any;
  qrCode: any;

  importSellerDetails: any;

  loading = true;

  Form: FormGroup;

  glTypes = [{ name: "Transaction Gls" }, { name: "All Gls" }];


  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  displayedColumns = ['accountname', 'openingBalance', 'debit', 'credit', 'closingBalance', 'action'];


  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private reportService: FinanceReportsService,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private datePipe: DatePipe
  ) { }

  pageFunction = "View";
  ngOnInit(): void {
    this.route.queryParams.subscribe((params) => {
      if (params.action) {
        const action = params.action;
        this.pageFunction = params.action;
      }
    });

    const today = new Date();
    const toDateDefault = today.toISOString().split("T")[0];
    const fromDateDefault = new Date(
      today.getFullYear(),
      today.getMonth() - 1,
      today.getDate()
    )
      .toISOString()
      .split("T")[0];
    this.Form = this.fb.group({
      type: ["PDF", Validators.required],
      fromDate: [fromDateDefault, [Validators.required]],
      toDate: [toDateDefault, [Validators.required]],
      glType: ["Transaction Gls"],
    });

    this.Form.valueChanges.subscribe(() => {
      this.getData();
    });

    this.getData();
  }



  //************************************************************************************** */
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }


  //************************************************************************************** */

  destroy$: Subject<boolean> = new Subject<boolean>();
  data: any;
  isLoading = false;
  getData() {

    this.data = {};



    this.isLoading = true;

    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    let params = {
      fromDate: this.datePipe.transform(this.Form.value.fromDate, "yyyy-MM-dd"),
      toDate: this.datePipe.transform(this.Form.value.toDate, "yyyy-MM-dd"),
      reportType: this.Form.value.glType,
    };

    console.log("params :", params);

    this.reportService
      .getTrialBalanceReportData(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.data :", res);
          if (res.entity) {
            this.data = res.entity;

            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
            this.isLoading = false;

            console.log("this.data :", this.data);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {
          this.isLoading = false;
        },
      }),
      Subscription;

  }
  // totals = {
  //   openingBalance: 0,
  //   debit: 0,
  //   credit: 0,
  //   closingBalance: 0
  // };
  // calculateTotals() {
  //   this.totals.openingBalance = this.data.reduce((total, item) => total + item.openingBalance, 0);
  //   this.totals.debit = this.data.reduce((total, item) => total + item.debit, 0);
  //   this.totals.credit = this.data.reduce((total, item) => total + item.credit, 0);
  //   this.totals.closingBalance = this.data.reduce((total, item) => total + item.closingBalance, 0);
  // }
  calculateTotal(field: string): number {
    return this.data.reduce((total, item) => total + item[field], 0);
  }
  viewDetails(data: any) {
    let route = "/erp-reports/fs-class-data";
    this.router.navigate([route], {
      queryParams: {
        fsClass: data.financial_grouping,
        fromDate: this.datePipe.transform(
          this.Form.value.fromDate,
          "yyyy-MM-dd"
        ),
        toDate: this.datePipe.transform(this.Form.value.toDate, "yyyy-MM-dd"),
        action: "View",
      },
    });
  }

  cancel() {
    this.router.navigate(["/erp-reports/dashboard"]);
  }

  posting: boolean = false;
  downloadDoc() {
    this.posting = true;
    let type = this.Form.value.type;
    let fromDate = this.datePipe.transform(
      this.Form.value.fromDate,
      "YYYY-MM-dd"
    );
    let toDate = this.datePipe.transform(
      this.Form.value.toDate,
      "YYYY-MM-dd"
    );

    const params = new HttpParams()
      .set("fromDate", fromDate)
      .set("toDate", toDate)
      .set("reportType", this.Form.value.glType);

    if (type == "PDF") {
      this.reportService
        .downloadTrialBalanceReport(params)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res :", res);
            let url = window.URL.createObjectURL(res.data);

            // if you want to open PDF in new tab
            window.open(url);

            let a = document.createElement("a");
            document.body.appendChild(a);
            a.setAttribute("style", "display: none");
            a.setAttribute("target", "blank");
            a.href = url;
            // Append row.code to the download URL
            a.download = `TrialBalance.pdf`;
            a.click();
            window.URL.revokeObjectURL(url);
            a.remove();
            this.snackbar.showNotification("snackbar-success", res.message);
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
            this.posting = false;
          },
          complete: () => {
            this.posting = false;
          },
        }),
        Subscription;
    } else if (type == "EXCEL") {
    }
  }

  home() {
    this.router.navigate(["/erp-dashboard/main"]);
  }


  reportDashboard() {
    this.router.navigate(["/erp-reports/dashboard"]);
  }



}
