import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RoutePrivilegeGuard } from '../erp-procurement/data/services/_AccessControlAuthGuard.service';
import { BalanceSheetComponent } from './modules/preview-reports/balance-sheet/balance-sheet.component';
import { FsClassDataComponent } from './modules/preview-reports/fs-class-data/fs-class-data.component';
import { GlAccountStatementComponent } from './modules/preview-reports/gl-account-statement/gl-account-statement.component';
import { ProfitLossComponent } from './modules/preview-reports/profit-loss/profit-loss.component';
import { TrialBalanceComponent } from './modules/preview-reports/trial-balance/trial-balance.component';
import { DashboardComponent } from './modules/dashboard/dashboard.component';
import { FinanceReportsComponent } from '../erp-finance/modules/finance-management/finance-reports/finance-reports.component';
import { ProcurementReportsComponent } from './modules/procurement-reports/procurement-reports.component';

const routes: Routes = [
  {
    path: "dashboard",
    component: DashboardComponent,
    canActivate: [],
  },


  {
    path: "procurement-reports",
    component: ProcurementReportsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: { clientName: 'ProcurementModule', requiredPrivilege: ["Dashboard"] },
  },

  {
    path: "finance-reports",
    component: FinanceReportsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: { clientName: 'ProcurementModule', requiredPrivilege: ["Dashboard"] },
  },

  {
    path: "preview-balance-sheet",
    component: BalanceSheetComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "FinanceModule",
      requiredPrivilege: [
        // "All Transactions",
        "All Currencies",
      ],
    },
  },


  {
    path: "preview-profit-loss",
    component: ProfitLossComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "FinanceModule",
      requiredPrivilege: [
        // "All Transactions",
        "All Currencies",
      ],
    },
  },


  {
    path: "preview-trial-balance",
    component: TrialBalanceComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "FinanceModule",
      requiredPrivilege: [
        // "All Transactions",
        "All Currencies",
      ],
    },
  },
  {
    path: "fs-class-data",
    component: FsClassDataComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "FinanceModule",
      requiredPrivilege: [
        // "All Transactions",
        "All Currencies",
      ],
    },
  },

  {
    path: "gl-account-statements",
    component: GlAccountStatementComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "FinanceModule",
      requiredPrivilege: [
        // "All Transactions",
        "All Currencies",
      ],
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ErpReportsRoutingModule { }
